/* color */

$secondary :#2A8356;
$primary:#EDF4EE;
$link-color: #121212;
$link-hover-color: $secondary;
$link-hover-decoration:   none;
$border-color:  #CDDDCD;


/*text */

$font-family-lato: 'Lato', sans-serif;
$font-family-Roboto: 'Roboto', sans-serif;

$font-family-base: $font-family-lato;



/*spacing*/
$spacer: 1rem ;
$spacers: (
  1: ($spacer * .375),
  2: ($spacer * 0.75),
  3: ($spacer * .5),
  4: $spacer,
  5: ($spacer * 1.5),
  6: ($spacer * 2),
  7: ($spacer * 2.5),
  8: ($spacer * 2.75),
  9: ($spacer * 3),
  10: ($spacer * 3.5),
  11: ($spacer * 4),
  'gap':($spacer * 0.25),
);



/* border radius */
$border-radius: 0.125rem;


$table-cell-padding : 18px 4px;
$table-hover-bg:$primary;


$pagination-border-color:$secondary;
$pagination-hover-bg: $primary;
$pagination-hover-border-color: $secondary;
$pagination-active-color:#fff;
$pagination-active-bg:$secondary;
$pagination-active-border-color: $secondary;

$modal-inner-padding: $spacer*2;
$modal-content-border-radius: 2px