.table tr{
    cursor: pointer;
}
.pagination{
    span{
        vertical-align: -webkit-baseline-middle;
    }
}
.close{
    right: 32px;
}
.modal__member{
    margin: calc((100vh - 212px)/2) auto;
}
.modal__img{
    @include size(141px);
    background: url('https://github.com/hexschool/webLayoutTraining1st/blob/master/week5/emir.jpeg?raw=true') no-repeat center;
    background-size: cover;
    flex-shrink: 0;
}