.logo{
    a{
        background:url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/7059003f6d011be52053cb5e63d68f3999af01d7/week5/logo-ANSWER.svg')no-repeat left ;
        background-size: contain;
        text-indent:101%; 
        overflow:hidden; 
        white-space:nowrap;
    }
}
.sidebar{
    min-width: 296px;
}
.sidebar__item{
    position: relative;
   
    a{
        transition: all 0.5s;
        &:hover{
            background-color:#CDDDCD;
           
        }
    }
} 
.sidebar__item--active{   
    position: relative;
    &::before{
        content: '';
        width: 4px;
        height: 36px;
        background-color: $secondary;
        position: absolute;
        left: 0;
      
    }
     a{
     color:$secondary;
    
     }
 }

 .user__img{
     img{
         @include size(40px)
     }
 }